	module.exports = {"data":{"challengeNode":{"title":"Problem 270: Cutting Squares","description":["A square piece of paper with integer dimensions N×N is placed with a corner at the origin and two of its sides along the x- and y-axes. Then, we cut it up respecting the following rules:","We only make straight cuts between two points lying on different sides of the square, and having integer coordinates.","Two cuts cannot cross, but several cuts can meet at the same border point.","Proceed until no more legal cuts can be made.","Counting any reflections or rotations as distinct, we call C(N) the number of ways to cut an N×N square. For example, C(1) = 2 and C(2) = 30 (shown below).","","","What is C(30) mod 108 ?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-270-cutting-squares","blockName":"Project Euler","tests":[{"text":"<code>euler270()</code> should return 82282080.","testString":"assert.strictEqual(euler270(), 82282080, '<code>euler270()</code> should return 82282080.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler270() {\n  // Good luck!\n  return true;\n}\n\neuler270();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-271-modular-cubes-part-1","id":"5900f47c1000cf542c50ff8e"},"slug":"/coding-interview-prep/project-euler/problem-270-cutting-squares"}}

